<template>
    <div id="dropdown-container" v-if="years.length && months.length">
        <label for="years-dropdown">Select Year: </label>
        <select
            id="years-dropdown"
            v-model="selectedYear"
            @change="calculateTotal"
        >
            <option v-for="year in years" :key="year" :value="year">
                {{ year }}
            </option>
        </select>

        <br /><br />

        <label for="months-dropdown">Select Month: </label>
        <select
            id="months-dropdown"
            v-model="selectedMonth"
            @change="calculateTotal"
        >
            <option v-for="month in months" :key="month" :value="month">
                {{ month }}
            </option>
        </select>

        <br /><br />

        <label for="percentage-input">Enter Percentage: </label>
        <div style="display: inline-block">
            <input
                type="number"
                id="percentage-input"
                v-model.number="selectedPercentage"
                @input="calculateTotal"
                min="1"
                max="100"
                step="1"
                style="width: 40px; text-align: center"
            />%
        </div>

        <hr />

        <p id="total-amount" v-if="totalAmount !== undefined">
            Total: ${{ totalAmount.toFixed(2) }}
        </p>
        <p id="selected-percent" v-if="selectedPercentAmount !== undefined">
            {{ selectedPercentage }}% of Total: ${{
                selectedPercentAmount.toFixed(2)
            }}
        </p>
        <p id="custom-amount" v-if="customAmount !== undefined">
            Total Tips: ${{ customAmount.toFixed(2) }}
        </p>
    </div>
</template>

<script>
import { ref, watch } from "vue";

export default {
    name: "TransactionDropdown",
    props: {
        transactions: Array,
    },
    setup(props) {
        const years = ref([]);
        const months = ref([]);
        const selectedYear = ref("");
        const selectedMonth = ref("");
        const totalAmount = ref(0);
        const customAmount = ref(0);

        const selectedPercentage = ref(8);
        const selectedPercentAmount = ref(0);

        const calculateTotal = () => {
            const typeColumn = "Type";
            const amountColumn = "Value (USD)";
            totalAmount.value = 0;
            customAmount.value = 0;
            props.transactions.forEach((transaction) => {
                const dateString = transaction["Transaction Date"].replace(
                    /["\\]/g,
                    "",
                );
                const date = new Date(dateString);
                const month = date.toLocaleString("en-US", { month: "long" });
                const year = transaction["Year"];
                const type = transaction[typeColumn];
                if (
                    month === selectedMonth.value &&
                    year === selectedYear.value
                ) {
                    let amount = transaction[amountColumn].replace(
                        /[$,"]/g,
                        "",
                    );
                    amount = parseFloat(amount);
                    if (type === '"BOOKING"' || type === '"CREDIT_REFUND"') {
                        totalAmount.value += amount;
                    } else if (type === '"CUSTOM"') {
                        customAmount.value += amount;
                    }
                }
            });
            selectedPercentAmount.value =
                totalAmount.value * (selectedPercentage.value / 100);
        };

        watch(
            () => props.transactions,
            (newTransactions) => {
                const monthsSet = new Set();
                const yearsSet = new Set();
                newTransactions.forEach((transaction) => {
                    const dateM = new Date(transaction["Transaction Date"]);
                    const year = transaction["Year"];
                    const month = dateM.toLocaleString("en-US", {
                        month: "long",
                    });
                    monthsSet.add(month);
                    yearsSet.add(year);
                });
                years.value = Array.from(yearsSet).sort();
                months.value = Array.from(monthsSet).sort();

                if (years.value.length > 0) selectedYear.value = years.value[0];
                if (months.value.length > 0)
                    selectedMonth.value = months.value[0];

                calculateTotal();
            },
            { immediate: true },
        );

        return {
            years,
            months,
            selectedYear,
            selectedMonth,
            totalAmount,
            customAmount,
            selectedPercentage,
            selectedPercentAmount,
            calculateTotal,
        };
    },
};
</script>

<style scoped>
#dropdown-container {
    text-align: center;
    margin-top: 20px;
}
</style>
