<template>
    <div>
        <DropArea @filesProcessed="handleFilesProcessed" />
        <TransactionDropdown
            v-if="transactions.length"
            :transactions="transactions"
        />
    </div>
</template>

<script>
import { ref } from "vue";
import DropArea from "./components/DropArea.vue";
import TransactionDropdown from "./components/TransactionDropdown.vue";

export default {
    components: {
        DropArea,
        TransactionDropdown,
    },
    setup() {
        const transactions = ref([]);

        const handleFilesProcessed = (processedTransactions) => {
            transactions.value = processedTransactions;
        };

        return { transactions, handleFilesProcessed };
    },
};
</script>

<style>
body {
    font-family: Consolas, monaco, monospace;
}
</style>
