<template>
    <div
        id="drop-area"
        @drop.prevent="dropHandler"
        @dragover.prevent="dragOverHandler"
        @dragleave="dragLeaveHandler"
        @click="clickHandler"
    >
        <img
            src="https://pbs.twimg.com/media/EmGmYLBUYAUb7wQ.jpg"
            style="max-width: 100px"
        />
        <p v-if="!dragging">Drag & Drop CSV files here</p>
        <p v-else>Drop here!</p>
        <input
            type="file"
            id="fileElem"
            accept=".csv"
            multiple
            style="display: none"
            @change="handleFiles"
        />
    </div>
</template>

<script>
import { ref } from "vue";

export default {
    emits: ["filesProcessed"],
    setup(props, { emit }) {
        const transactions = ref([]);
        const dragging = ref(false);
        const handleFiles = (event) => {
            const files = event.target.files;
            processFiles(files);
        };

        const dropHandler = (event) => {
            event.preventDefault();
            const files = event.dataTransfer.files;
            processFiles(files);
            dragging.value = false; // Reset dragging state
        };

        const dragOverHandler = (event) => {
            console.log("dragging");
            event.preventDefault();
            dragging.value = true; // Set dragging state to true
            console.log(dragging);
        };
        const dragLeaveHandler = (event) => {
            event.preventDefault();
            dragging.value = false;
            console.log(dragging);
        };
        const clickHandler = () => {
            document.getElementById("fileElem").click();
        };

        const processFiles = (files) => {
            transactions.value = [];
            for (let i = 0; i < files.length; i++) {
                const file = files[i];
                const reader = new FileReader();
                reader.onload = (event) => {
                    const result = event.target.result;
                    const fileTransactions = parseCSV(result);
                    transactions.value.push(...fileTransactions);
                    if (i === files.length - 1) {
                        emit("filesProcessed", transactions.value);
                    }
                };
                reader.readAsText(file);
            }
        };

        const parseCSV = (csv) => {
            const lines = csv.split("\n");
            const headers = lines[0].split(",");
            const transactions = [];
            for (let i = 1; i < lines.length; i++) {
                const values = lines[i].split(",");
                const transaction = {};
                for (let j = 0; j < headers.length; j++) {
                    if (headers[j] === "Date of Game") {
                        const date = new Date(values[j]);
                        transaction["Year"] = date.getFullYear();
                    } else {
                        transaction[headers[j]] = values[j];
                    }
                }
                transactions.push(transaction);
            }
            console.log(transactions);
            return transactions;
        };

        return {
            handleFiles,
            dropHandler,
            dragOverHandler,
            dragLeaveHandler,
            dragging,
            clickHandler,
        };
    },
};
</script>

<style scoped>
#drop-area {
    border: 2px dashed #ccc;
    border-radius: 20px;
    width: 300px;
    padding: 20px;
    text-align: center;
    margin: 20px auto;
    cursor: pointer;
}
</style>
